.contacts {
  padding: 120px 0 90px 0;

  &__wrapper {
    padding: 0 35px 0 52px;
    display: grid;
    grid-template-columns: 400px 585px;
    justify-content: space-between;
  }

  &__photo {
    img {
      position: relative;
      left: -25px;
			height: 100%;
			object-fit: scale-down;
    }
  }

  &__descr {
    padding-top: 6px;

    .divider {
      margin: 0;
    }
  }

  &__title {
    position: relative;
    padding: 1px 0 0 5px;
    @include before_circle;
  }

  &__text,
  &__social {
    margin-top: 20px;
  }

  &__link {
    margin-right: 25px;

    img {
      width: 30px;
			height: 30px;
			margin: 10px 0;
      border-radius: 100%;
      transition: all 0.6s;

      &:hover {
        box-shadow: 2px 3px 10px rgba(0, 0, 0, 0.5);
      }
    }

    &:hover {
      text-decoration: none;
    }
  }

  &__form {
    margin-top: 30px;
    display: grid;
    grid-template: 45px 190px 45px / repeat(2, 280px);
    justify-content: space-between;
    row-gap: 27px;

    label {
      display: block;
      position: absolute;
      top: -9px;
      left: 12px;
      height: 18px;
      padding: 0 8px;
      background-color: #fff;
      font-size: 12px;
      margin-bottom: 0;
    }
  }

  &__triggers {
		display: grid;
		grid-column: 1 / 3;
		grid-template-columns: repeat(2, auto);
		column-gap: 40px;
	}
	&__policy {
		display: flex;
		align-items: center;
		font-size: 12px;
		line-height: 14px;

		input {
			margin-right: 10px;
		}
	}

  &__input {
    position: relative;

    input {
      width: 100%;
      height: 100%;
      padding: 0 20px;
      font-size: 12px;
    }
  }

  &__textarea {
    position: relative;
    grid-column: 1 / 3;
    textarea {
      width: 100%;
      height: 100%;
      resize: none;
      padding: 20px;
      font-size: 12px;
    }
  }

  &__btn {
    border: none;
    font-weight: bold;
    font-size: 14px;
    line-height: 21px;
		@include hover_effect;
  }
}
