  .certificates {
    .container {
      .certificates__wrapper {
        display: grid;
        justify-content: center;
        margin-top: 100px;

        .certificates__button {
          padding: 12px 45px;
          height: 50px;
          font-size: 20px;
          text-transform: uppercase;
          letter-spacing: 1px;
          border: none;
          @include hover_effect;
        }
      }
    }
  }

    // BLOCK: --- Modal window
    .modal_show {
      display: block;
    }
    .modal_hide {
      display: none;
    }

    .modal {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      overflow: hidden;
      background-color: rgba(0, 0, 0, 0.5);
      z-index: 1000;

      &__wrapper {
        position: relative;
        width: 100%;
        max-height: 100vh;
        overflow-y: auto;

        padding-top: 120px;
        display: grid;
        row-gap: 50px;
        justify-items: center;
      }

      &__close {
        position: absolute;
        top: 5px;
        right: 5px;
        padding: 10px;
        width: 76px;
        height: 76px;
        cursor: pointer;

        &__close-window {
          width: 100%;
          height: 100%;
        }
      }

      &__link {
        max-width: 700px;
        margin: 0px 10px;

        &_img {
          width: 100%;
        }
      }
    }
